import ReactDOM from 'react-dom';
import Login from './login';
import {RegisterAdd} from './components/register'
import './auth-style.css';

function Register() {
  return (
    <div className="register-page">
      <div className="window-centering">
        <div className="auth-window">
          <div className="auth-title">Registracija</div>
          <input id="email" className="auth-input" placeholder="Elektroninis paštas" maxLength="20"/>
          <input id="username" className="auth-input" placeholder="Prisijungimo vardas" maxLength="20"/>
          <input id="password" type="password" className="auth-input" placeholder="Slaptažodis" maxLength="20"/>
          <button className="auth-button" onClick={() => RegisterAdd()}>Registruotis</button>
          <button className="side-button" onClick={() => ReactDOM.render(<Login/>, document.getElementById('root'))}>← grįžti atgal</button>
        </div>
      </div>
    </div>
  );
}

export default Register;
