import firebase from 'firebase';
import { getDate, sendMail } from './functions';

export function RegisterAdd(){
    var email, username, password, tempID, ID, tempData, tempKey, exists, user;
    ID = "";
    exists = 0;
    email = document.getElementById("email").value;
    username = document.getElementById("username").value;
    password = document.getElementById("password").value;
    if((email !== "")&&(username !== "")&&(password !== "")){
        if(username.length >= 8){
            if(password.length >= 8){
                firebase.database().ref("users").once("value", (snapshot) => {
                    snapshot.forEach((childSnap) => {
                        tempKey = childSnap.key;
                        childSnap.forEach((childChildSnap) => {
                            tempData = childChildSnap.val();
                            console.log(tempData);
                            if(tempData.username === username){
                                exists = 1;
                            }
                        })
                    })
                    if(exists === 0){
                        if(tempData === undefined){
                            ID = "00001";
                            user = "user1";
                        }
                        else{
                            tempID = "" + (parseInt(tempData.uuid) + 1);
                            for(var i = (ID.length + tempID.length); i < 5; i++){
                                ID = ID + "0"
                            }
                            ID = ID + tempID;
                            user = "user" + (parseInt(tempKey.slice(4)) + 1)
                        }
                        firebase.database().ref("users/" + user + "/data").set({
                            email: email,
                            username: username,
                            password: password,
                            uuid: ID,
                            date: getDate(),
                            status: "unconfirmed"
                        })
                        sendMail(email, "Registracija", "Sėkmingai prisiregistravote, norint naudotis paskyra prisijunkite ir įveskite registracijos kodą.<br/><br/> Registracijos kodas: ");
                    }
                    else{
                        console.log("Vartotojas tokiu vardu jau yra");
                    }
                })   
            }
            else{
                console.log("Slaptažodis turi bent 8 simbolių ilgio");
            }
        }
        else{
            console.log("Vardas turi bent 8 simbolių ilgio");
        }
    }
    else{
        console.log("Neįvesti visi duomenys");
    }
}